.coin-container .content {
    max-width: 1140px;
    margin: 1rem auto;
    padding: 0.7rem 1rem;
    display: flex;
    flex-direction:  column;
    background-color: #26272b;
    box-shadow: 0 0 12px #18191b;
    border-radius: 8px;
}


.rank {
    margin: .5 0;
}

.rank-btn {
    border:1px solid #6900ff;
    box-shadow: 0 0 8px #6900ff;
    background-color: #6900ff;
    border-radius: 8px;
    padding: 0.2rem;
    margin-right: 0.5rem;
}

.info {
    display: grid;
    grid-template-columns: repeat(2,1fr);
}



.info .coin-heading {
    display: flex;
    align-items: center;
    margin: 1rem 0;
}

.info .coin-price {
    display: flex;
    align-items: center;
    justify-content: center;
}

.info p {
    padding-right: 1rem;
}

table {
    margin: 0.5rem 0;
}
td, th {
    padding: 8px;
    text-align: center;
}
.try {
    width: 100%;
}
th {
    background-color: #333;
}

.stats {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2rem;
    width: 100%;
}

.stats .row {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #808080;
    margin: .6rem 0;
    padding-bottom: .5rem;
}

.stats .row p:first-child {
    color: yellow;
    /*background-color: yellow;*/
}

.about h3 {
    margin: 1rem 0;
}
.copy-clipboard {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 700px) {
    .coin-container .content {
        max-width: 100%;
        margin: 0.5rem;
        padding: 0 .5rem;
    }

    .stats {
        grid-template-columns: 1fr;
    }

    td, th {
        padding: 3px;
    }
    .table-img{
        width: 30px;
        height: 30px;
    }
}

@media screen and (max-width: 450px) {
    .chart-mobile{

        /*height: 300px;*/
    }

    .coin-price h1{
        font-size: 1.2rem;
    }
    .coin-heading {
        font-size: 0.87rem;
    }
    tr th {
        font-size: 0.9rem;
        padding: 0;
    }
    tr td {
        font-size: 0.7rem;
        /*word-break: break-all*/
        /*border: solid 1px white;*/
    }

    .table-img{
        width: 20px;
        height: 20px;
        border-radius: 50%;
    }
}