/*Style for individual Coins*/
.coin-row {

    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #26272b;
    box-shadow: 0 0 12px #18191b;
    margin: 2rem 1rem;
    padding: .7rem 0.2rem;

}

.coin-row:hover {
    transform: scale(1.02);
    transition-duration: 0.4s;
    box-shadow: 0 0 11px rgba(235, 226, 226, 0.795);
    cursor: pointer;
}

.coin-list {
    display: grid;
    grid-template-columns: repeat(6, 1fr); /* Create 6 columns with equal width */
    gap: 10px; /* Adjust the gap between columns as needed */
}

img {
    height: 30px;
    /*margin-right: 8px; !*the white space around the multi carousel images*!*/
    width: auto;
    margin-right: 0.3rem;
}

.img-symbol {
    display: flex;
    align-items: center;
}

@media screen and (max-width: 720px) {
    .hide-mobile {
        display: none;
    }

    .coin-list {
        display: grid;
        grid-template-columns: repeat(4, 2fr); /* Create 6 columns with equal width */
        gap: 10px; /* Adjust the gap between columns as needed */
    }
    .heading {
        display: grid;
        grid-template-columns: repeat(4, 2fr); /* Create 6 columns with equal width */
        gap: 10px; /* Adjust the gap between columns as needed */
    }

    .address {
       /*flex-wrap: wrap;*/
        word-break: break-word;
        /*overflow-wrap: break-word;*/
    }

}

