/* Add this CSS to your styles */
.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 20px;
}

.cardSearch {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    word-break: break-word;
    font-size: 0.7rem;
    height: auto;
    width: 260px;
    padding: 10px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.nftCard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    word-break: break-word;
    font-size: 0.7rem;
    height: auto;
    width: 160px;
    padding: 10px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.search-img{
    width: 200px;
    height: 200px;
}
.nftImg{
    width: auto;
    height: auto;
}
