.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 10px;
  display: flex;
  flex-direction: column;
  width: 250px;
  justify-content: center;
  align-items: center;
  padding: 0;
  /*gap: 5px;*/
  /*text-align: center;*/
  border-radius: 0%; /* might change it*/

}
.card-img{
  width: auto;
  height: auto ;
  border-radius: 140px;

}
.card:hover {
  transform: scale(1.02);
  transition-duration: 0.4s;
  box-shadow: 0 0 11px rgba(235, 226, 226, 0.795)
}
.card:hover .card-img{
  transform: scale(0.92);
  transition-duration: 0.4s;
}
.carousel {
  display: flex;
  /*flex-wrap: wrap;*/
  /*background-color: whitesmoke;*/
  justify-content: space-between;
  /*margin: 2rem;*/
  /*padding: 1rem;*/
  /*  This centers the arrows*/
}
.carousel-item{
  /*background-color: red;*/
  /*border: solid 2px blue;*/
  /*margin: 0px;*/
  /*padding: auto;*/
  display: flex;
  justify-content: center;
  align-items: center;
}
.reset-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /*background-color: whitesmoke;*/
  margin: 1rem;
}
.reset-button {
  width: 7rem;
  text-align: center;
  background-color: lightslategrey;
  border-radius: 8px;
}

.reset-button:hover {
  background-color: dimgrey;
}

.content-space{
  display: flex;
  justify-content: center;
  align-items: center;
}

.sparkline {
  max-width: 1140px;
  margin: 1rem auto;
  padding: 0.7rem 1rem;
  display: flex;
  flex-direction:  column;
  background-color: #18191b;
  box-shadow: 0 0 12px #b7193f;
  border-radius: 8px;
}
.arrowDown {
  display: flex;
  flex-direction: column;
  font-size: 0.8rem;
}

.arrowUp{
  display: flex;
  flex-direction: column;
  font-size: 0.8rem;
  margin-bottom: 4px;
}
.formattedPercentages {
  display: flex;
  justify-content: center;
  align-items: center;
}
.table-row {
  display: table-row
}

