/* Navbar.css */

/* Navbar.css */
.centerItems {
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar {
    background-color: #26272b;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-logo h1 {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 1.7rem;
    margin: 0;
}

.icon {
    margin-right: 10px;

}

.navbar-links {
    display: flex;
    align-items: center;
}

.nav-link {
    color: #fff;
    text-decoration: none;
    margin-right: 20px;
    font-size: 16px;
}

.search-container {
    display: flex;
    align-items: center;
}

.dark-input {
    background-color: #1a1b1e;
    color: #fff;
    padding: 8px;
    border: none;
    border-radius: 4px;
    margin-right: 10px;
}

button {
    background-color: #4caf50;
    color: #fff;
    border: none;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
}
.dropdown-mobile{
    display: none;
}





.dropdown {
    float: left;
    overflow: hidden;
}
